.focus-on {
  #screen-meta,
  #screen-meta-links,
  #wp-toolbar,
  #wpfooter,
  .page-title-action,
  .postbox-container > *,
  .update-nag,
  .wrap > h1 {
    opacity: 0;
    transition-duration: .6s;
    transition-property: opacity;
    transition-timing-function: ease-in-out;
  }
  div {
    &.error,
    &.notice,
    &.updated {
      opacity: 0;
      transition-duration: .6s;
      transition-property: opacity;
      transition-timing-function: ease-in-out;
    }
  }
  #wp-toolbar {
    opacity: 0.3;
  }
}
.focus-off {
  #screen-meta,
  #screen-meta-links,
  #wp-toolbar,
  #wpfooter,
  .page-title-action,
  .postbox-container > *,
  .update-nag,
  .wrap > h1 {
    opacity: 1;
    transition-duration: .2s;
    transition-property: opacity;
    transition-timing-function: ease-in-out;
  }
  div {
    &.error,
    &.notice,
    &.updated {
      opacity: 1;
      transition-duration: .2s;
      transition-property: opacity;
      transition-timing-function: ease-in-out;
    }
  }
  #wp-toolbar {
    -webkit-transform: translate(0, 0);
  }
}
.focus-on {
  #adminmenuback,
  #adminmenuwrap {
    transition-duration: .6s;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    transition-timing-function: ease-in-out;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
}
.focus-off {
  #adminmenuback,
  #adminmenuwrap {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    transition-duration: .2s;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    transition-timing-function: ease-in-out;
  }
}
